
/*
 * Global Imports
 *
 * Add here styles that need to be added in the main.scss (dist/css/styles.css) file
 */

.content-hidden {
  display: none;
}

body {
  background: #f5f5f5;
}

#page-wrapper {
  margin: 2% 15%;
  background-color: #ffffff;

  .container {
    &.full-width {
      padding-inline: unset;
      padding: 0;
    }
  }

  #block-blog-history-breadcrumbs, #block-blog-history-content {
    grid-column: 1 / 13;
  }

  footer.site-footer {
    background: #2ab7ca;
    border: unset;
    min-height: 115px;
  }
}

#block-blog-history-site-branding {
  margin: 0;
  width: 100%;
  background: transparent;
  border-top: 10px solid #2ab7ca;
  min-height: 130px;

  .site-branding__inner {
    height: unset;
    flex-direction: column;
    align-items: flex-start;

    .logo-wrapper {
      img {
        width: 120px;
        height: 120px;
        max-width: 120px;
        max-height: 120px;
      }
    }

    .site-branding__text {
      padding: 30px 0 20px 0;
      font-weight: bold;
      font-size: 45px;
      font-family: 'Noto sans', sans-serif;
      text-align: left;
      color: #283655;
      line-height: 55px;
    }

    .site-branding__divider {
      text-align: left;
      border-bottom: 6px solid #1e1f26;
      width: 110px;
      margin-bottom: 70px;
    }
  }

}
